import $ from "jquery";

export default class Chunk {
    /*更改位置*/
   static creat(j, i,pos,size) {
        let left = j * pos;
        let top = i * pos;
        if (!Chunk.chunks[j][i]) {
            let chunk = $(`<div></div>`);
            chunk.width(size);
            chunk.height(size);
            chunk.css({
                "backgroundColor": color,
                "position": "absolute",
                "left": left,
                "top": top
            });
            Chunk.chunks[j][i] = chunk;
        }else {
            let chunk = Chunk.chunks[j][i];
            chunk.width(size);
            chunk.height(size);
            chunk.css({
                "backgroundColor": color,
                "position": "absolute",
                "left": left,
                "top": top
            });
        }

    }
}
Chunk.chunks = [0,0];
