import Game from "./Game";
import $ from "jquery";

export default class Level {
    static creat() {
        for (let i = 0; i < 15; i++) {
            for (let j = 0; j < 15; j++) {
                let chunk = $(`<div></div>`);
                if (Level.chunks[j]) {
                    Level.chunks[j].push(chunk);
                } else {
                    Level.chunks[j] = [];
                    Level.chunks[j].push(chunk);
                }
                Level.chunks[j][i].css({"display":"none"})
                chunk.appendTo("#game");
            }
        }
    }

    static level(count, color, difColor) {
        let size = Game.mapSize / count - 5;
        let pos = Game.mapSize / count;
        let posx = Math.floor(Math.random()*count);
        let posy = Math.floor(Math.random()*count);
        for (let i = 0; i < count; i++) {
            for (let j = 0; j < count; j++) {
                let chunk;
                chunk = Level.chunks[j][i];
                let left = j * pos;
                let top = i * pos;
                chunk.width(size);
                chunk.height(size);
                chunk.css({
                    "display":"block",
                    "backgroundColor": color,
                    "position": "absolute",
                    "left": left,
                    "top": top
                });
                if (posx === j && posy === i) {
                    chunk.css({"backgroundColor": difColor});
                    chunk.on("click", () => {
                        if(!Game.gameStart){
                            return;
                        }
                        Game.level++;
                        $("#level").html("当前关卡："+Game.level);
                        chunk.off();
                        if (Game.level === 2) {
                            Level.level(4, "#000000", "#D0D0D0");
                        } else if (Game.level === 3) {
                            Level.level(5, "#0302c6", "#fbfaff");
                        } else if (Game.level === 4) {
                            Level.level(6, "#99ffd2", "#ffffff");
                        } else if (Game.level === 5) {
                            Level.level(7, "#b4abab", "#a1a1aa");
                        } else if (Game.level === 6) {
                            Level.level(8, "#4342dc", "#010095");
                        } else if (Game.level === 7) {
                            Level.level(9, "#21212d", "#010095");
                        } else if (Game.level === 8) {
                            Level.level(10, "#18188e", "#ba16a7");
                        } else if (Game.level === 9) {
                            Level.level(11, "#b857c9", "#cd2690");
                        } else if (Game.level === 10) {
                            Level.level(12, "#679320", "#addb60");
                        } else if (Game.level === 11) {
                            Level.level(13, "#c1865f", "#e8cb9e");
                        } else if (Game.level === 12) {
                            Level.level(14, "#c65252", "#3e0606");
                        } else if (Game.level === 13) {
                            Level.level(15, "#901c1c", "#010095");
                        } else if (Game.level === 14) {
                            Level.level(15, "#6b6ae8", "#010095");
                        } else if (Game.level === 15) {
                            Level.level(15, "#2d9255", "#2dd900");
                        } else if (Game.level === 16) {
                            Level.level(15, "#42bbe0", "#010095");
                        } else if (Game.level === 17) {
                            Level.level(15, "#bfff7d", "#52a80a");
                        } else if (Game.level === 18) {
                            Level.level(15, "#77e348", "#010095");
                        } else if (Game.level === 19) {
                            Level.level(15, "#cd4c67", "#c2bfec");
                        } else if (Game.level === 20) {
                            Level.level(15, "#5ccea7", "#ededf6");
                        } else if (Game.level === 21) {
                            Level.level(15, "#5eac34", "#aaaad0");
                        } else if (Game.level === 22) {
                            Level.level(15, "#60ac3f", "#010095");
                        } else if (Game.level === 23) {
                            Level.level(15, "#8b8ba5", "#bdbdee");
                        } else if (Game.level === 24) {
                            Level.level(15, "#c9c9e7", "#747318");
                        } else if (Game.level === 25) {
                            Level.level(15, "#7b9bdc", "#7e7cd0");
                        } else if (Game.level === 26) {
                            Level.level(15, "#e2e26f", "#d0d069");
                        } else if (Game.level === 27) {
                            Level.level(15, "#7f144a", "#b11b49");
                        } else if (Game.level === 28) {
                            Level.level(15, "#1a6980", "#231292");
                        } else if (Game.level === 29) {
                            Level.level(15, "#871348", "#b41111");
                        } else if (Game.level === 30) {
                            Level.level(15, "#e8e35f", "#c4923f");
                        } else if (Game.level === 31) {
                            Level.level(15, "#7474a5", "#8d6d4c");
                        }else {
                            alert("能来到这里真是了不起，恭喜通关")
                        }
                    });
                }
            }
        }
    }

}
Level.chunks = [];
