import $ from "jquery";
export default class Game {
    static die() {
        this.gameStart = false;
        $("#gameOver").css({display:"block"})
    }
    /*开始游戏*/
    static startGame() {
        let time = $("#time");
        time.html("剩余时间: "+Game.gameTime)
        Game.timer = setInterval(()=>{
            time.html("剩余时间: "+Game.gameTime)
            if(Game.gameTime <=0){
                clearInterval(Game.timer);
                Game.die();
                console.log("sile",Game.gameTime);
            }
            Game.gameTime -=1;
        },1000)
        Game.level = 1;
        this.gameStart = true;
        $("#gameStart").html("重新开始");
        $("#gameOver").css({display:"none"});
        $("#level").html("当前关卡："+Game.level);
    }
}
Game.gameStart = false;
Game.gameTime = 20;
Game.mapSize = 600;
Game.level = 1;
Game.timer;

